import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './index.css';


const App = () => {
    const [num, setNum] = useState();
    const [name, setName] = useState();
    const [moves, getMove] = useState();

    useEffect(() => {
        async function getData() {
            const respoApi = await axios.get(`https://pokeapi.co/api/v2/pokemon/${num}`);
            setName(respoApi.data.name);
            getMove(respoApi.data.moves.length);
        }
        getData();
    })
    
    return (
        <>
            
            <h1>You Choose <span> {name}</span> Pok√©mon. 
            </h1>
            <h1>moves of <span> {name} </span> are <span> {moves}</span>
            </h1>
            <select value={num} onChange={(event) => {
                setNum(event.target.value);
            }}>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
                <option value="25">25</option>

            </select>
      </>      
    );
       
       
    
}

export default App;